*{
    margin: 0;
}
header{
    background-color: red;
}
.heading{
    margin: 10px;
}
.cert-container{
    display: flex;
    justify-content: space-between;
}
.live li{
    display: flex;
    flex-direction: column;
    background-color: rgba(4, 5, 4, 0.865);
    color: rgb(255, 255, 255);
    flex-wrap: wrap;
    border: 1px black;
    align-content: space-around;
    border-radius: 10px;
    padding: 10px;
    margin: 5px;
}
.live ul{
    display: flex;
    flex-direction: column;
}
.live li:hover{
    background-color: rgb(155, 155, 155);
    color:rgb(0, 0, 0);
    transition: 0.9s;  
}
.solo li{
    display: flex;
    background-color: rgba(4, 5, 4, 0.865);
    color: rgb(255, 255, 255);justify-content: center;
    border: 1px black;
    
    border-radius: 10px;
    
    padding: 10px;
    margin: 5px;
}
.solo li:hover{
    background-color: rgb(155, 155, 155);
    color:rgb(0, 0, 0);
    
    transition: 0.9s;
}
.great li{
    border-radius: 10px;
    background-color: rgba(4, 5, 4, 0.865);
    color: rgb(255, 255, 255);
    padding: 10px;
    margin: 5px;
    margin-left:45px;
    display: flex;
    justify-content: center;
}
.great li:hover{
    background-color: rgb(162, 162, 162);
    color: rgb(0, 0, 0);
    transition: 0.9s;
}

button{
    background-color:rgb(1, 86, 101);
    color: whitesmoke;
    padding: 20px;
    border-radius: 2pc;
}
button img{
    height:30px;
    width: 60px;
}

:root {
    --Head-bg: rgb(8, 89, 95);
    --cr-bg:rgb(212, 212, 212);
    --ht:350px;
    --wd : 470px;

  }
body{
    background-color: rgba(197, 197, 197, 0.202);
}
.cHeading{
    background-color: rgb(8, 89, 95);
    background-color: var(--Head-bg);
    border-radius: 10px;
    padding: 5px;
    color: white;
    font-size: 20pt;
}
.cer-cont{
    background-color:rgb(212, 212, 212);
    background-color:var(--cr-bg);
    border: 2px thin rgb(101, 101, 101);
    border-radius: 10px;
}
.cer-cont img{
    height: 350px;
    height: var(--ht);
    width:470px;
    width:var(--wd);
    border-radius: 10px;
    border: 1px black thin;
    padding: 1%;
    opacity: .7;
}
.cer-cont img:hover{
    opacity: 1;
    background-color: rgb(0, 0, 0);
}
.cer-box .logo{
   height: 100px;
   width: 200px;
   margin-top: 5em;
}
.cer-box .log:hover{
    background-color: rgb(0, 0, 0);
 }
.cer-box{
    display: flex;
    flex-wrap: wrap;
    justify-content:space-around;
    margin: 10px;
    border: 2px solid rgba(3, 101, 128, 0.962);
    padding: 10px;
    border-radius: 2ex; 
}
.gt-hack{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.cert-container{
    display: flex;
    flex-direction: column;
}
p{
    color: hsl(184, 85%, 10%);
    font-size: 2ch;
}
